*{
    margin:0;
    padding:0;
    box-sizing:border-box;
}

.header__div{
    border-bottom-left-radius: 5.5rem;
    border-bottom-right-radius: 5.5rem;
}

.header__brand{
    font-size: 1.45rem;
}

.header__logo{
    width: 65px;
}

.header__selected{
    width: 30px;
}

.footer__img{
    width: 45px;
}

.home__img{
    width: 130px;
}

.home__div{
    border-radius: 10rem;
}

.registrar__img{
    width: 105px;
}

.form__div{
    border-radius: 5rem;
}

.custom-file-input:lang(en)~.custom-file-label::after{
    content: "Seleccionar";
}

.main-btn{
    width: 150px;
}

.btn-alert{
    font-size: 1.3rem;
    line-height: 1.1;
}

.cardmascota{
    border-radius: 1.5rem;
    max-width: 16rem;
    min-width: 12rem;
}

.cardmascota__header{
    border-radius: calc(1.5rem - 1px) calc(1.5rem - 1px) 0 0 !important;
}

.cardmascota__footer{
    border-radius: 0 0 calc(1.5rem - 1px) calc(1.5rem - 1px) !important;
}

.cardmascota__img {
    width: 100%;
    height: 15vw;
    object-fit: cover;
}

.buscar__div{
    border-radius: 2rem;
}

.css-yk16xz-control{
    font-size: 1rem !important;
    font-weight: 400 !important;
    line-height: 1.5 !important;
    color: #5a5a5a !important;
    background-color: #fff !important;
    background-clip: padding-box !important;
    border: 1px solid #ced4da !important;
    border-radius: .4rem !important;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out !important;
    outline: none !important;
    cursor: pointer !important;
}

.css-1pahdxg-control{
    color: #5a5a5a !important;
    background-color: #fff !important;
    border-color: #d0eae2 !important;
    outline: 0 !important;
    box-shadow: 0 0 0 0.2rem rgb(120 194 173 / 25%) !important;
}

.css-12jo7m5 {
    color: hsl(0deg 0% 50%) !important;
}

.css-1rhbuit-multiValue {
    background-color: hsl(163deg 38% 62% / 24%) !important;
    border-radius: 10px !important;
}

.css-xb97g8 {
    border-radius: 10px !important;
}

.tablemascota__img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 50%;
}

.admin__header__logo {
    width: 45px;
}

.collapse__icon:focus {
    outline: none !important;
}